.service {
  margin-bottom: 96px;
}
@media only screen and (max-width: 1300px) {
  .service {
    margin-bottom: 36px;
  }
}
.service__wrapper {
  display: flex;
  gap: 64px;
}
@media only screen and (max-width: 1500px) {
  .service__wrapper {
    gap: 24px;
  }
}
@media only screen and (max-width: 1300px) {
  .service__wrapper {
    flex-direction: column;
  }
}
.service__preview {
  width: calc(40% - 32px);
  min-height: 450px;
  align-self: stretch;
  position: relative;
  overflow: hidden;
  border-radius: 16px;
}
.service__preview img {
  position: absolute;
  height: 100%;
  width: 100%;
  inset: 0;
  object-fit: cover;
  object-position: center center;
}
@media only screen and (max-width: 1300px) {
  .service__preview img {
    object-position: bottom center;
  }
}
@media only screen and (max-width: 1300px) {
  .service__preview {
    display: none;
  }
}
.service__content {
  width: calc(70% - 32px);
}
.service__content h2 {
  font-size: 42px;
  font-weight: 600;
  line-height: 53.55px;
  text-align: left;
  text-transform: uppercase;
}
@media only screen and (max-width: 1300px) {
  .service__content h2 {
    font-size: 32px;
    line-height: 43.55px;
  }
}
.service__content p {
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: left;
}
@media only screen and (max-width: 1300px) {
  .service__content {
    width: 100%;
  }
}
.service__cards {
  display: flex;
  margin-top: 42px;
  gap: 12px;
}
@media only screen and (max-width: 800px) {
  .service__cards {
    flex-direction: column;
    margin-top: 24px;
  }
}
.service__cards img {
  width: 42px;
  height: 42px;
}
.service__cards div {
  width: calc(33.333% - 8px);
  background-color: #EDEDED;
  padding: 24px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
@media only screen and (max-width: 800px) {
  .service__cards div {
    width: 100%;
    flex-direction: row;
    align-items: center;
  }
}
.service__cards p {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  text-align: left;
  margin: 0;
}