@charset "UTF-8";
.faq {
  padding: 62px 42px;
  background-color: rgba(24, 25, 31, 0.0784313725);
  border-radius: 16px;
}
@media only screen and (max-width: 1300px) {
  .faq {
    padding: 24px;
  }
}
.faq__caption {
  font-size: 42px;
  font-weight: 600;
  line-height: 53.55px;
  text-align: left;
  text-transform: uppercase;
}
@media only screen and (max-width: 1200px) {
  .faq__caption {
    font-size: 28px;
    line-height: 36px;
  }
}
.faq__subcaption {
  font-size: 20px;
  font-weight: 300;
  line-height: 30px;
  text-align: left;
  color: rgba(24, 25, 31, 0.6392156863);
}
.faq__wrapper {
  display: flex;
  gap: 12px;
  width: 100%;
}
@media only screen and (max-width: 900px) {
  .faq__wrapper {
    flex-direction: column;
  }
}
.faq__content {
  display: flex;
  width: calc(50% - 6px);
  flex-direction: column;
  gap: 12px;
}
@media only screen and (max-width: 900px) {
  .faq__content {
    width: 100%;
  }
}
.faq__content details {
  overflow: hidden; /*    */
  padding: 24px;
  background-color: rgba(255, 255, 255, 0.6392156863);
  border-radius: 16px;
}
@media only screen and (max-width: 1200px) {
  .faq__content details {
    padding: 16px;
  }
}
.faq__content details > div {
  padding: 24px 40px;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;
  text-align: left;
}
@media only screen and (max-width: 1200px) {
  .faq__content details > div {
    padding: 16px 0;
  }
}
.faq__content summary {
  display: flex;
  justify-content: flex-start;
  cursor: pointer;
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  text-align: left;
  list-style: none; /*   */
}
.faq__content summary::-webkit-details-marker {
  display: none; /*    */
}
.faq__content summary::before {
  content: "+"; /*   + */
  color: #de0000;
  font-size: 30px;
  margin-right: 24px;
}
@media only screen and (max-width: 1200px) {
  .faq__content summary::before {
    font-size: 24px;
    margin-right: 14px;
  }
}
.faq__content details[open] summary::before {
  content: "-"; /*    -   */
}