.contacts {
  margin: 60px 0;
  padding: 20px 0;
  background-image: url("../img/b3.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
@media only screen and (max-width: 1300px) {
  .contacts {
    margin: 30px 0;
  }
}
.contacts__wrapper {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}
@media only screen and (max-width: 900px) {
  .contacts__wrapper {
    flex-direction: column;
  }
}
.contacts__item {
  position: relative;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-radius: 30px;
  padding: 20px;
  border: 1px solid #f5f5f5;
  background-color: #fff;
}
@media only screen and (max-width: 900px) {
  .contacts__item {
    width: 100%;
  }
}
.contacts__item img {
  object-fit: cover;
}
.contacts__item a {
  color: #fff;
  font-size: 23px;
  line-height: 28px;
  margin-bottom: 32px;
}
.contacts__item span {
  font-weight: 600;
}
.contacts__item:first-child {
  background-color: #de0000;
  color: #fff;
}